'use strict';

import menuBlock from './modules/menu.js';
import promoBlock from './modules/promo.js';
import swiperBlock from './modules/swiper.js';
import post from './modules/POST.js';
import cursesBlock from './modules/curses.js';
import pageUp from './modules/pageUp.js';


import {
    modal
} from './modules/modal.js';


window.addEventListener('DOMContentLoaded', () => {
    const preload = document.querySelector('.preload');

    function ready() {
        document.querySelector('body').removeAttribute('style');
        preload.remove();
    }

    window.addEventListener("load", ready);

    menuBlock('.menu__item_link', '.menu__logo', 'bigLogo', '.menu__wrapper', 'flex-end', '.hamburger', 'hamburger__active', '.menu__wrapper', 'menu__active', '.hamburger__overlay', 'hamburger__overlay__active', '.hamburger__logo', 'hamburger__logo_active');
    promoBlock('.promo__video');
    swiperBlock('.swiper', '.swiper-pagination');
    cursesBlock('.curses__items', '.curses__accordion_heading', '.line1', '.line2', 'active-style', 'active-content', 'active_line1', 'active_line2');
    post('form', '#modal-offline', '#modal-thanks', '.overlay', 'body');
    pageUp('.pageup');

    modal('.modal__close', '.offline__descr_btn');
})