import {
    Swiper
} from "../services/swiper-bundle.min"

function swiperBlock(trigger, pagination) {
    const swiper = new Swiper(trigger, {
        slidesPerView: 'auto',
        centeredSlides: true,
        roundLengths: true,
        loop: true,
        loopAdditionalSlides: 30,

        autoplay: {
            delay: 5000,
            pauseOnMouseEnter: true,
            disableOnInteraction: false,
        },

        pagination: {
            el: pagination,
            clickable: true,
        },
        breakpoints: {
            767: {
                spaceBetween: 40,
                slidesPerView: 3,

            }
        }
    })
}

export default swiperBlock;