import {
    actionModal
} from "./modal";


function post(trigger, modal, modalThanks, overlay, body) {

    const forms = document.querySelectorAll(trigger);
    forms.forEach(form => {
        form.addEventListener('submit', function (e) {
            e.preventDefault();

            fetch('mailer/smart.php', {
                    method: 'POST',
                    body: new FormData(this)
                })
                .then(function (response) {
                    if (response.ok) {
                        form.querySelectorAll('input').forEach(input => {
                            input.value = '';
                        });
                        actionModal('close', modal, overlay, body);
                        actionModal('open', modalThanks, overlay, body);

                        setTimeout(function () {
                            actionModal('close', modalThanks, overlay, body);
                        }, 5000);

                        form.reset();
                    }
                })
                .catch(error => {
                    console.log('Error:', error);
                });

            return false;
        });
    })
}

export default post;