function cursesBlock(containerBtn, btn, lineOne, lineTwo, activeStyle, activeContent, activeLine1, activeLine2) {

    const parentBtns = document.querySelector(containerBtn);

    parentBtns.addEventListener('click', (e) => {
        const btns = e.target.closest(btn);
        if (btns) {
            const line1 = btns.querySelector(lineOne);
            const line2 = btns.querySelector(lineTwo);
            btns.classList.toggle(activeStyle);
            btns.nextElementSibling.classList.toggle(activeContent);
            line1.classList.toggle(activeLine1);
            line2.classList.toggle(activeLine2);

            if (btns.classList.contains(activeStyle)) {
                if (window.innerWidth < 768) {
                    btns.nextElementSibling.style.maxHeight = '1200px';
                } else {
                    btns.nextElementSibling.style.maxHeight = '475px';
                }

            } else {
                btns.nextElementSibling.style.maxHeight = '0px';
            }
        }
    })
}
export default cursesBlock;